
import { defineComponent } from "vue";
import { APM202001SS02_REQ, APM202001SS02_RES_List } from "@/shared/type/APM202001SS02.type"
import pagination from "@/shared/components/Pagination/Pagination.vue";
import { PAGE_SIZE } from "@/shared/constants/common.const";
import '@vuepic/vue-datepicker/dist/main.css'
import VueDatePicker from '@vuepic/vue-datepicker';
import { DataCenter } from "@/shared/services/DataCenter";

export default defineComponent({
    name: "apm202001ss02",
    components: {
        pagination,
        VueDatePicker
    },
    inject: ['searchTypeCode'],
    data() {
        return {
            myApprovalLists: [] as APM202001SS02_RES_List[],
            totalCount: 0 as number,
            startIndex: 1 as number,
            search: {
                employeeNo: "",
                pageNumber: 1,
                pageSize: PAGE_SIZE,
                searchTypeCode: this.$route.query.filter ? this.$route.query.filter.toString() != '01' ? '' : '01' : '',
                fromDate: '' as unknown,
                toDate: '' as unknown,
                authorizationStatusCode: this.$route.query.status ? this.$route.query.status.toString() : ''
            },
            isResetPage: true as boolean,
            headers: [
				'No.',
				'View',
				'Status',
				'Authorization No',
				'Authorization Request reason',
				'Branch/Dept',
				'Requested By',
				'Reason',
				'1st Approver',
				'2nd Approver',
				'3rd Approver',
				'4th Approver',
				'5th Approver',
				'Create Date',
			]
        }
    },
    watch: {
        searchTypeCode : {
            handler(searchTypeCode: string) {
                this.search.searchTypeCode = searchTypeCode
            },
            deep: true
        }
    },
    computed: {
        statusTypeCode(): {key: string, value: string}[] {
            return this.$store.state.CommonCodeModule.commonCode.AuthorizationStatusCode
        },
        formattedStartDate(): string {
			return this.search.fromDate ? this.$datetime.formatVueDatePicker(this.search.fromDate as Date) : ''
		},
		formattedEndDate(): string {
			return this.search.toDate ? this.$datetime.formatVueDatePicker(this.search.toDate as Date) : ''
		},
    },
    created() {
		if (this.$route.query.filter !== undefined && this.$route.query.tab !== undefined ) {
			if(this.$route.query.tab && this.$route.query.filter != null){
				if(this.$route.query.tab.toString() == 'my-approval'){
					this.search.searchTypeCode = this.$route.query.filter.toString()
					this.requestMyApprovalData(true, this.search.authorizationStatusCode, this.search.searchTypeCode);
				} else {
					this.requestMyApprovalData(false, this.search.authorizationStatusCode, this.search.searchTypeCode);
				}
			}
		} else {
			this.requestMyApprovalData(false, this.search.authorizationStatusCode, this.search.searchTypeCode);
		}
        // if (this.$route.query.filter !== undefined && this.$route.query.tab !== undefined) {
		// 	if (this.$route.query.tab && this.$route.query.filter != null) {
		// 		if (this.$route.query.tab.toString() == 'my-request') {
		// 			this.search.authorizationStatusCode = this.$route.query.filter.toString()
		// 			this.requestMyRequestLists(true, this.search.authorizationStatusCode, '');
		// 		} else {
		// 			this.requestMyRequestLists(false, this.search.authorizationStatusCode, '');
		// 		}
		// 	}
		// } else {
		// 	this.requestMyRequestLists(false, this.search.authorizationStatusCode, '');
		// }
    },
    methods: {
        async requestMyApprovalData(isSearch: boolean, authorizationStatusCode: string, searchTypeCode: string) {
            console.log(authorizationStatusCode);
            console.log(searchTypeCode);
            
            const body: APM202001SS02_REQ = {
                authorizationRequestEmpNo: '',
                authorizationNo: 0,
                fromDate: '',
				toDate: '',
                authorizationBizTypeCode: '',
                employeeNo: await DataCenter.getData('employeeNo') as string || '',
                searchTypeCode: isSearch ? searchTypeCode : "",
                pageNumber: isSearch ? 1 : this.search.pageNumber,
                pageSize: this.search.pageSize,
                authorizationStatusCode: isSearch ? authorizationStatusCode : "",
            }
            const res: any = await this.$request.getRequest("APM20200111", body, false);
            if (res.header.result) {
                this.myApprovalLists = res.body.list;
                this.totalCount = res.body.totalCount;
            }
        },
        handleClearSearch() {
            this.search.authorizationStatusCode = "";
            this.search.employeeNo = "";
            this.search.pageNumber = 1;
            this.search.pageSize = PAGE_SIZE;
            this.search.searchTypeCode = "";
            this.search.fromDate = ''
			this.search.toDate = ''
            this.requestMyApprovalData(false, this.search.searchTypeCode, this.search.authorizationStatusCode);
        },

        handleView(data: APM202001SS02_RES_List) {
            if(data.linkScreenNo.length > 13){
                this.$router.push({
                    path: `/${data.linkScreenNo.slice(0,13)}`,
                    query: {
                        approvalType: 'my-approval',
                        approvalStatus: data.authorizationStatusCode,
                        authorizationNo: data.authorizationNo,
                        employeeNo: data.authorizationRequestEmpNo,
                        searchTypeCode: this.search.searchTypeCode,
                        tab: `${data.linkScreenNo.slice(13,26)}`
                    }
                })
            } else {
                this.$router.push({
                    path: `/${data.linkScreenNo}`,
                    query: {
                        approvalType: 'my-approval',
                        approvalStatus: data.authorizationStatusCode,
                        authorizationNo: data.authorizationNo,
                        employeeNo: data.authorizationRequestEmpNo,
                        searchTypeCode: this.search.searchTypeCode
                    }
                })
            }
        },
        handlePagination(pageNumber: number) {
            this.search.pageNumber = pageNumber;
            this.requestMyApprovalData(false, this.search.searchTypeCode, this.search.authorizationStatusCode);
        },

        handleStartIndex(startIndex: number) {
            this.startIndex = startIndex;
        },

        handleConvertAuthStatusCode(code: string): string {
            return this.$codeConverter.convertValue("AuthorizationStatusCode", code)
        },
    }
})

